.ToDo{
    padding: 5px;
    margin: auto;
    background-color: #57CC99;
    display: flex;
    flex-direction: row;
    justify-content:space-between;
    border-bottom: 4px  solid #22577A;
    border-radius: 10px;
    font-size: 1.7rem;
    font-weight: bold;
    width: 98%;
}

.id{
    color: #0B0500;
    width: 30px;
    margin: auto 10px;
}

.task{
    color: #0B0500;  
    width:80%;
    text-align: justify;
}

.checkBox{
    cursor: pointer;
    margin: auto 5px;
}

.deleteBtn{
    margin: auto 0;
    width: 90px;
    height: 35px;
    text-align: center;
    font-size: 1.2rem;
    font-weight: bold;
    border-radius: 10px;
    
}